import {AppAnimate, AppSearchBar, AppsPagination} from '@crema';
import {Box, Button, Hidden} from '@mui/material';
import DamagedTable from './DamagedTable';
import PropTypes from 'prop-types';
import ListEmptyResult from '@crema/core/AppList/ListEmptyResult';
import AppsHeader from '@crema/core/AppsContainer/AppsHeader';
import AppsContainer from '@crema/core/AppsContainer';

const ReportDamagedProductUI = ({
  onPageChange,
  page,
  count,
  onSearchOrder,
  messages,
  loading,
  onAddDamagedProductClicked,
  damagedData,
}) => {
  return (
    <AppAnimate animation='transition.slideUpIn'>
      <AppsContainer fullView>
        <AppsHeader>
          <Box
            display='flex'
            flexDirection='row'
            alignItems='center'
            width={1}
            justifyContent='space-between'
          >
            <AppSearchBar
              iconPosition='right'
              overlap={false}
              onChange={(event) => onSearchOrder(event.target.value)}
              placeholder={messages['common.searchHere']}
            />
            <Box display='flex' flexDirection='row' alignItems='center'>
              <Button
                onClick={() => onAddDamagedProductClicked()}
                variant='contained'
                color='primary'
              >
                {messages['ecommerce.addDamaged']}
              </Button>

              <Hidden smDown>
                <AppsPagination
                             rowsPerPage={40}

                  count={count}
                  page={page}
                  onPageChange={onPageChange}
                />
              </Hidden>
            </Box>
          </Box>
        </AppsHeader>
        {loading ? (
          <ListEmptyResult loading={true} />
        ) : (
          <DamagedTable damagedData={damagedData} />
        )}
      </AppsContainer>
    </AppAnimate>
  );
};
export default ReportDamagedProductUI;
ReportDamagedProductUI.propTypes = {
  count: PropTypes.number.isRequired,
  page: PropTypes.number.isRequired,
  onPageChange: PropTypes.func,
  onSearchOrder: PropTypes.func,
  messages: PropTypes.object,
  loading: PropTypes.bool,
  damagedData: PropTypes.array,
  onAddDamagedProductClicked: PropTypes.func,
};
