import React, {useState} from 'react';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import DamagedActions from './DamagedActions';
import {styled} from '@mui/material/styles';
import EditDamagedProduct from '../EditDamagedProductModal/EditDamagedProductModal';
import {useNavigate} from 'react-router-dom';
const StyledTableCell = styled(TableCell)(() => ({
  fontSize: 14,
  padding: 8,
  '&:first-of-type': {
    paddingLeft: 20,
  },
  '&:last-of-type': {
    paddingRight: 20,
  },
}));

const TableItem = ({data}) => {
  const navigate = useNavigate();
  const [open, setOpen] = useState(false);
  const onClose = () => {
    setOpen(false);
  };
  const handelOpenDamagedProduct = (key) => {
    navigate('/product-management/product_detail/' + key, {
      state: data,
    });
  };
  return (
    <>
      <TableRow key={data.damagedId} className='item-hover'>
        <StyledTableCell component='th' scope='row'>
          <Box
           onClick={() => handelOpenDamagedProduct(data.productId)}
            sx={{
              color: 'primary.main',
              borderBottom: (theme) =>
                `1px solid ${theme.palette.primary.main}`,
              display: 'inline-block',
              fontWeight: 'bold',
              fontSize: '15px',
              cursor: 'pointer',
            }}
          >
            {data.id}
          </Box>
        </StyledTableCell>
        <StyledTableCell align='left'>
          {data.color === 'null' ? data.color + '| ' : ''}{' '}
          {data.size === 'null' ? data.size + '| ' : ''} count: {data.count}
        </StyledTableCell>
        <TableCell onClick={() => handelOpenDamagedProduct(data.productId)}>
          <Box
            sx={{
              color: 'primary.main',
              display: 'inline-block',
              fontWeight: 'bold',
              fontSize: '15px',
              cursor: 'pointer',
            }}
          >
            {data.productId}
          </Box>
        </TableCell>
        <TableCell>{data.auther}</TableCell>
        <TableCell>{new Date(data.time).toLocaleString()}</TableCell>

        <TableCell align='right'>
          <DamagedActions data={data} openEdit={() => setOpen(true)} />
        </TableCell>
      </TableRow>
      <EditDamagedProduct initialValues={data} onClose={onClose} open={open} />
    </>
  );
};

export default TableItem;

TableItem.propTypes = {
  data: PropTypes.object.isRequired,
};
