import jwtAxios from '@crema/services/auth/jwt-auth';
import {useEffect, useState} from 'react';
import {useIntl} from 'react-intl';
import AddDamagedProductModal from './AddDamagedProductModal/AddDamagedProductModal';
import ReportDamagedProductUI from './ReportDamagedProductUI';
import {useDispatch} from 'react-redux';
import {showError} from 'redux/actions';

const ReportDamagedProduct = () => {
  const dispatch = useDispatch();
  const [damagedData, setDamagedData] = useState([]);
  const [page, setPage] = useState(0);
  const [index, setIndex] = useState();
  const [isNext, setIsNext] = useState();
  const [loading, setLoading] = useState(false);
  const [damagedProductCount, setDamagedProductCount] = useState(0);
  const {messages} = useIntl();
  const [addModal, setAddModal] = useState(false);

  useEffect(() => {
    getDamagedProduct();
  }, [page]);

  const getDamagedProduct = () => {
    setLoading(true);

    jwtAxios
      .get('/api/product/damaged', {
        params: {
          page: page,
          index: page ? index : undefined,
          isNext: isNext,
        },
      })
      .then((data) => {
        setDamagedData(data.data.data);
        setDamagedProductCount(data.data.length);
        setIndex(data.data.lastVisible);
        setLoading(false);
      })
      .catch(() => {
        dispatch(showError('no data to review'));
        setLoading(false);
        setDamagedData([]);
        setDamagedProductCount(0);
      });
    return 0;
  };

  useEffect(() => {
    setPage((prev) => (prev == 0 ? getDamagedProduct() : 0));
  }, [addModal]);
  const onPageChange = (event, value) => {
    if (value > page) {
      setIsNext(true);
    } else {
      setIsNext(false);
    }

    setPage(value);
  };

  const onSearchDamaged = () => {};
  const onAddDamagedProductClicked = () => setAddModal(!addModal);

  return (
    <>
      <ReportDamagedProductUI
        onSearchOrder={onSearchDamaged}
        onPageChange={onPageChange}
        messages={messages}
        page={page}
        count={damagedProductCount}
        loading={loading}
        damagedData={damagedData}
        onAddDamagedProductClicked={onAddDamagedProductClicked}
      />
      <AddDamagedProductModal open={addModal} onClose={setAddModal} />
    </>
  );
};
export default ReportDamagedProduct;
